.auth__submit-btn {
  
    background-color: #fff;
    border-radius: 2px;
    min-height: 50px;
    color: #000;
    font-weight: normal;
    font-size: 18px;
    line-height: 22px;
    padding: 0;
    border: none;
    width: 100%;
    margin-top: 216px;
    margin-bottom: 15px;
    transition: background-color .2s;
  }
  
  .auth__submit-btn:hover {
    opacity: 0.85;
    cursor: pointer;
  }
  
  @media screen and (max-width: 424px) {
    .auth__submit-btn {
      margin-top: 143px;
      font-size: 16px;
      line-height: 19px;
      padding-bottom: 20px
    }
  }